import { Box, Typography } from '@mui/material';
import { useTeacherSelector } from '../../store';
import { NotificationButton } from '../NotificationButton';
import { useDispatch } from 'react-redux';
import { changeTeacherName, confirmChangeTeacherName, startChangeTeacherName } from '../../commands/editable/editable';
import { selectIsTeacherNameEditing, selectTeacherName } from '../../selectors/editable';
import { HOC } from '../../../common/hoc/types';
import { TextField, TextFieldType } from '../../../common/component/TextField';
import { ClickOutsideHandler } from '../../../common/component/ClickOutsideHandler';
import { Header } from '../../../common/component/Header';


export const SubjectListHeader: HOC = () => {
  const dispatch = useDispatch();
  const displayName = useTeacherSelector(selectTeacherName);
  const isTeacherNameEditing = useTeacherSelector(selectIsTeacherNameEditing);

  return (
    <ClickOutsideHandler onClickOutside={() => dispatch(confirmChangeTeacherName.action())}>
      <Header>
        {isTeacherNameEditing ? 
            <Box width="100%" overflow="auto" height="100%">
              <TextField
                type={TextFieldType.header} 
                text={displayName}
                onChange={(text) =>  dispatch(changeTeacherName.action(text))}
              />
            </Box>
          :
          <Typography
            noWrap
            textOverflow='ellipsis'
            overflow='hidden'
            maxWidth='17rem'
            onClick={() => dispatch(startChangeTeacherName.action())}
          >
            {displayName ? displayName : 'click to add your name'}
          </Typography>
        }
        <NotificationButton />
      </Header>
    </ClickOutsideHandler> 
  );
}
