import { Box } from '@mui/system';
import React from 'react';
import styled from '@emotion/styled';
import { useDispatch } from 'react-redux';
import { subcectListViewSubjects, subjectListViewAbonements } from '../../commands/navigation/navigation';
import { useTeacherSelector } from '../../store';
import { selectSubjectListView } from '../../selectors/novigation';
import { SubjectListView } from '../../store/modal';
import { ButtonType, IconNavButton } from '../../../common/component/IconNavButton';

const NavigationContainer = styled(Box)`
    display: flex;
    height: 80;
    align-items: center;
    padding: 0.5rem;
    justify-content: space-around;
`;

export const SubjectListNavigation: React.FC = () => {
    const dispach = useDispatch();
    const subjectView = useTeacherSelector(selectSubjectListView);
    const isSubjectsActive = subjectView === SubjectListView.subjects;
    const isAbonementsActive = subjectView === SubjectListView.abonements;

    return (
        <NavigationContainer>
            <IconNavButton
                type={ButtonType.abonements}
                onClick={() => dispach(subjectListViewAbonements.action())}
                title="abonements"
                isActive={isAbonementsActive}
            />
            <IconNavButton
                type={ButtonType.subjects}
                onClick={() => dispach(subcectListViewSubjects.action())}
                title="subjects"
                isActive={isSubjectsActive}
            />
        </NavigationContainer>
    );
}
