import { onAuthStateChanged } from 'firebase/auth';
import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { auth } from '../../../common/api/firebaseConnection';
import { selectIsUserAuthenticated } from '../../../common/selectors/commonSelectors';
import { alreadyAuthenticatedUser } from '../../commands/authentication';
import { selectTeacherView } from '../../selectors/novigation';
import { useTeacherSelector } from '../../store';
import { TeacherView } from '../../store/modal';
import { Authentication } from '../Authentication';
import { LessonPage } from '../LessonPage';
import { SubjectList } from '../SubjectList';
import { SubjectPage } from '../SubjectPage';
import { TeacherFooter } from '../TeacherFooter';
import { TeacherSchedule } from '../TeacherSchedule';
import { startTeacherApp } from '../../commands/domain/domain';
import { HOC } from '../../../common/hoc/types';

export const AuthenticatedTeacherApp: HOC = () => {
    return (
        <>
            <TeacherWindow />
            <TeacherFooter />
        </>
    );
}

export const TeacherApp: React.FC = () => {

    const dispatch = useDispatch();
    useEffect(() => {
        const unsubscribe = onAuthStateChanged(auth, (user) => {
          if (user) {
            dispatch(alreadyAuthenticatedUser.action(user));
          }

          dispatch(startTeacherApp.action());
    
          return () => unsubscribe();
        });
    }, [dispatch]);

    const isUserAuthenticated = useTeacherSelector(selectIsUserAuthenticated);

    return (
        <>
            {
                isUserAuthenticated ? (
                    <AuthenticatedTeacherApp />
                ) : (
                    <Authentication />
                )
            }
        </>
    );
}

const TeacherWindow: HOC = () => {
    const navigation = useTeacherSelector(selectTeacherView);
    switch (navigation) {
        case TeacherView.schedule: {
            return  <TeacherSchedule />
        }
        case TeacherView.subjectList: {
            return <SubjectList />
        }
        case TeacherView.subjectPage: {
            return <SubjectPage />
        }
        case TeacherView.lessonPage: {
            return <LessonPage />
        }
        default: return <></>
    }
}
