import { Alert, Box, Typography } from '@mui/material';
import { useTeacherSelector } from '../../store';
import { selectAbonementOffersSubjectsFormatted, selectTeacherViewAbonementOffers } from '../../selectors/domain';
import { useDispatch } from 'react-redux';
import { startDeletingAbonement, startEditingAbonementOffer } from '../../commands/domain/domain';
import { IAbonementOffer } from '../../../common/types/domain';
import { HOC } from '../../../common/hoc/types';
import { Card } from '../../../common/component/Card';
import { IconButtonMenu } from '../../../common/component/IconButtonMenu';
import { TitleStyledTypography } from '../../../common/component/LessonHeader';

export enum AbonementsListControlsClasses {
  startEditingAbonementOffer = 'startEditingAbonementOffer',
  startDeletingAbonement = 'startDeletingAbonement',
};

interface IAbonementOfferCardProps extends IAbonementOffer {
  subjectsFormatted: string;
  editCommand: () => void;
  deleteCommand: () => void;
}

const AbonementOfferCard: React.FC<IAbonementOfferCardProps> = ({ id, price, limitLessons, subjectsFormatted, editCommand, deleteCommand  }) => {

  const limitLessonsText = limitLessons ? `${limitLessons} ${limitLessons > 1 ? 'Lessons' : 'Lesson' }` : '';

  return (
  <Card
    key={id}
    children={[
      <TitleStyledTypography
        key={`${id}_subjectsFormatted`}
      >
        {subjectsFormatted}
      </TitleStyledTypography>,
      <IconButtonMenu
        key={`${id}_menu`}
        options={[
          {title: 'Edit', command: editCommand},
          {title: 'Delete', command: deleteCommand},
        ]}
      />,
      <Typography
        key={`${id}_price`}
      >
        {price}
      </Typography>,
      <Typography
        key={`${id}_limitLessons`}
      >
        {limitLessonsText}
      </Typography>
    ]}
    onClick={editCommand}
  />)
}

export const AbonementsList: HOC = () => {
  const dispatch = useDispatch();
  const abonementOffers = useTeacherSelector(selectTeacherViewAbonementOffers);
  const abonementOffersSubjectsFormatted = useTeacherSelector(selectAbonementOffersSubjectsFormatted);
  return (
    <Box height={'100%'} overflow={'auto'}>
      {!!abonementOffers.length ? 
        abonementOffers.map((abonementOffer) => {
          return (
            <AbonementOfferCard 
              {...abonementOffer}
              key={abonementOffer.id}
              subjectsFormatted={abonementOffersSubjectsFormatted.get(abonementOffer.id) || ''} 
              editCommand={() => dispatch(startEditingAbonementOffer.action(abonementOffer.id))} 
              deleteCommand={() => dispatch(startDeletingAbonement.action(abonementOffer.id))}
            />
          )
        }) : 
        <Alert severity="info">Add your abonement offer</Alert>
      }
    </Box>
  );
}
