import styled from '@emotion/styled';
import { Box, Typography } from '@mui/material';
import React, { useRef, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useTeacherSelector } from '../../store';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import { 
  selectIsSubjectDescriptionEditing, 
  selectSubjectDescription 
} from '../../selectors/editable';
import { selectIsSubjectShowMore } from '../../selectors/novigation';
import { 
  changeSubjectDescription, 
  confirmChangeSubjectDescription, 
  startChangeSubjectDescription 
} from '../../commands/editable/editable';
import { subjectShowMore } from '../../commands/navigation/navigation';
import { 
  descriptionDefaultHeight, 
  selectSubjectDescriptionHeight, 
  subjectDescriptionContenerClassName 
} from '../../selectors/dom';
import { HOC } from '../../../common/hoc/types';
import { TextField, TextFieldType } from '../../../common/component/TextField';
import { ClickOutsideHandler } from '../../../common/component/ClickOutsideHandler';
import { TextButton } from '../../../common/component/TextButton';

export enum SubjectDescriptionControlsClasses {
  startChangeSubjectDescription = 'startChangeSubjectDescription',
  subjectShowMore = 'subjectShowMore',

};

const DescriptionContainer = styled(Box)`
  padding: 0 1rem;
  box-sizing: content-box;
  left: 0;
`;

export const SubjectDescription: HOC = () => {
  const descriptionContainerRef = useRef<HTMLDivElement | null>(null);
  const [hasShowMore, setHasShowMore] = React.useState(false);

  const subjectDescription = useTeacherSelector(selectSubjectDescription);
  const isDescriptionEditing = useTeacherSelector(selectIsSubjectDescriptionEditing);
  const showMore = useTeacherSelector(selectIsSubjectShowMore);
  const maxHeight = useTeacherSelector(selectSubjectDescriptionHeight);

  const dispatch = useDispatch();

  useEffect(() => {
    if (descriptionContainerRef.current) {
      const container = descriptionContainerRef.current;
      const contentHeight = container.scrollHeight;
  
      setHasShowMore(contentHeight > descriptionDefaultHeight);
      container.scrollTop = 0;
    }
  }, [subjectDescription, showMore, isDescriptionEditing]);

  return (
    <Box
      className={subjectDescriptionContenerClassName}
    >
      <ClickOutsideHandler onClickOutside={() => dispatch(confirmChangeSubjectDescription.action())}>
        <DescriptionContainer
          ref={descriptionContainerRef}
          minHeight={60}
          maxHeight={ maxHeight }
          overflow={ showMore ? "auto" : "hidden" }
        >
          {isDescriptionEditing ?
            <TextField 
              type={TextFieldType.textArea}
              text={subjectDescription}
              onChange={(e) => dispatch(changeSubjectDescription.action(e))}
            /> : 
            <Typography
              style={{ wordWrap: 'break-word' }}
              gutterBottom
              onClick={()=>dispatch(startChangeSubjectDescription.action())}
            >
              {subjectDescription ? subjectDescription : 'add description your subject'}
            </Typography>
          }
        </DescriptionContainer>
      </ClickOutsideHandler>
      { (hasShowMore && !isDescriptionEditing) && (
        <Box 
          className='show-more-wrapper' 
          display="flex" 
          justifyContent="center"
        >
          <TextButton
            title='See more'
            onClick={() => dispatch(subjectShowMore.action())}
          >
            { showMore ? <KeyboardArrowUpIcon  /> : <KeyboardArrowDownIcon /> }
          </TextButton>
        </Box>
      )}
    </Box>
  );
}
