import { Box, Button } from '@mui/material';
import React from 'react';
import { useDispatch } from 'react-redux';
import { finalizeLesson } from '../../commands/external/external';
import { useTeacherSelector } from '../../store';
import { selectLessonStatus } from '../../selectors/external';
import { LessonStatus } from '../../../common/types/domain';
import { footerHeight } from '../../../common/component/Footer';

export enum FinalizeLessonControlsClasses {
  finalizeLesson = 'finalizeLesson'
};

export const FinalizeLesson: React.FC = () => {
  const dispatch = useDispatch();
  const canFinalize: boolean = useTeacherSelector(selectLessonStatus) === LessonStatus.finished;
  return (
    <>
      {canFinalize &&     
        <Box 
          width='100%'
          display='flex'
          justifyContent='center'
          position='absolute'
          bottom={`${footerHeight + 16}px`}
        >
          <Button
            variant='contained' 
            color='success'
            onClick={() => dispatch(finalizeLesson.action())}
          >
            {`Finalize`}
          </Button>
        </Box>
      }
    </>
  );
}
