import React from 'react';
import { useTeacherSelector } from '../../store';
import { selectTeacherViewSubjects } from '../../selectors/domain';
import { ISubjectView } from '../../../common/types/teacherDomainView';
import { Box } from '@mui/system';
import { Alert } from '@mui/material';
import { useDispatch } from 'react-redux';
import { startDeletingSubject, startEditingSubject } from '../../commands/domain/domain';
import { getPeriodicityMessage, getTimeRepresentation } from '../../../common/utilities/getWeekDayRepresentation';
import { Card } from '../../../common/component/Card';
import { IconButtonMenu } from '../../../common/component/IconButtonMenu';
import { TitleStyledTypography } from '../../../common/component/LessonHeader';

interface ISubjectCardProps extends ISubjectView {
  editCommand: () => void;
  deleteCommand: () => void;
};

const SubjectCard: React.FC<ISubjectCardProps> = ({name, rules, editCommand, deleteCommand}) => {

  const representationsRule: string[] = rules.map((rule) => {
    const ruleText = getPeriodicityMessage(rule.periodicity);

    const timeStartText = getTimeRepresentation(rule.periodicity, rule.timeStart);

    return `${ruleText} at ${timeStartText}`;
  });

  const ruleFormatted = representationsRule.join(', ');

  return (
    <Card
      children={[
        <TitleStyledTypography key='subjectName'>{name}</TitleStyledTypography>,
        <IconButtonMenu
          key='subjectMenu' 
          options={[
            {title: 'Edit', command: editCommand},
            {title: 'Delete', command: deleteCommand},
          ]} 
        />,
        <TitleStyledTypography
          key='subjectRulesInfo'
        >
          {ruleFormatted}
        </TitleStyledTypography>,
      ]}
      onClick={editCommand}
    />
  )
}

export const SubjectListList: React.FC = () => {
  const dispatch = useDispatch();
  const subjects = useTeacherSelector(selectTeacherViewSubjects);

  return (
    <Box height={'100%'} overflow={'auto'}>
      {!!subjects.length ?
        subjects.map((subject) => {
          return (
            <SubjectCard
              key={subject.id}
              editCommand={() => {dispatch(startEditingSubject.action(subject.id))}}
              deleteCommand={() => {dispatch(startDeletingSubject.action(subject.id))}}
              {...subject}
            />
          )
        }): 
        <Alert severity="info">Add your subject</Alert>
      }
    </Box>
  );
}
