import React from 'react';
import { useTeacherSelector } from '../../store';
import { selectEditingRulePreviewHasIntersections, selectPreviewSchedule } from '../../selectors/editable';
import { Box } from '@mui/material';
import { selectCurrentDate, selectTeacherFullCalendarView } from '../../selectors/novigation';
import { useDispatch } from 'react-redux';
import { onChengeDateBackward, onChengeDateForward, onToday } from '../../commands/external/external';
import { Calendar } from '../../../common/component/Calendar';

export const RulePreviewCalendar: React.FC = () => {

  const previewLessons = useTeacherSelector(selectPreviewSchedule);
  const isIntersection = useTeacherSelector(selectEditingRulePreviewHasIntersections);
  const currentDate = useTeacherSelector(selectCurrentDate);
  const teacherCalendarRepresentationType = useTeacherSelector(selectTeacherFullCalendarView);

  
  const dispatch = useDispatch();


    return (
        <Box
            className='rule-preview-calendar'
            position='absolute'
            top={0}
            height='50%'
            width='100%'
            zIndex={1300}
            bgcolor='#fff'
            overflow='auto'
        >
            <Calendar 
                lessons={previewLessons} 
                representationType={teacherCalendarRepresentationType} 
                onLessonClick={()=>undefined}
                warningOnOverlapingEvents={isIntersection}
                startDate={currentDate}
                onChengeDateBackward={() => dispatch(onChengeDateBackward.action())}
                onChengeDateForward={() => dispatch(onChengeDateForward.action())}
                onToday={() => dispatch(onToday.action())}
            />
        </Box>
    );
}
