import { useDispatch } from 'react-redux';
import { useTeacherSelector } from '../../store';
import { AbonementType, id } from '../../../common/types/domain';
import { 
  selectAbonementOfferAbonementSubjects, 
  selectAbonementOfferAbonementType, 
  selectAbonementOfferLimitLessons, 
  selectAbonementOfferLimitTime, 
  selectAbonementOfferPrice 
} from '../../selectors/editable';
import { selectTeacherSubjectSelectOptions } from '../../selectors/domain';
import { confirmSavingAbonementOffer } from '../../commands/domain/domain';
import { closeAbonementModal, toggleRuleModalMode } from '../../commands/navigation/navigation';
import { selectIsAbonementModal } from '../../selectors/novigation';
import { 
  changeAbonementLimitLessons, 
  changeAbonementLimitTime, 
  changeAbonementPrice, 
  changeAbonementSubjects, 
  changeAbonementType 
} from '../../commands/editable/editable';
import { toggleStringArrayValue } from '../../../common/commands/toggleArrayValue';
import { Box } from '@mui/material';
import { HOC } from '../../../common/hoc/types';
import { Modal } from '../../../common/component/Modal';
import { ChipsSelect, ISelectOption } from '../../../common/component/ChipsSelect';
import { Select } from '../../../common/component/Select';
import { NumberField } from '../../../common/component/NumberField';
import { ButtonType, IconNavButton } from '../../../common/component/IconNavButton';
import { ModalMode } from '../../../common/component/types';

export const SubjectListAbonementOfferModal: HOC = () => {

  const dispatch = useDispatch();

  const isAbonementModalOpen: boolean = useTeacherSelector(selectIsAbonementModal);

  const limitLessons: number = useTeacherSelector(selectAbonementOfferLimitLessons);
  const limitTime: number = useTeacherSelector(selectAbonementOfferLimitTime);
  const price: number = useTeacherSelector(selectAbonementOfferPrice);
  const abonementType: AbonementType = useTeacherSelector(selectAbonementOfferAbonementType);
  const abonementOfferSubjects: id[] = useTeacherSelector(selectAbonementOfferAbonementSubjects);
  const subjectSelectOptions: ISelectOption[] = useTeacherSelector(selectTeacherSubjectSelectOptions);

  const isUnlimited = abonementType === AbonementType.unlimited;
  const modalMode = isAbonementModalOpen ? ModalMode.fillHeight : ModalMode.closed;

  const changeAbonementOfferSubjects = (id: string) => {
    const selectredSubjectsId = toggleStringArrayValue(abonementOfferSubjects, id);
    dispatch(changeAbonementSubjects.action(selectredSubjectsId));
  };

  const comandChilden = [
    <IconNavButton
      key={'confirm'}
      onClick={() => dispatch(confirmSavingAbonementOffer.action())}
      type={ButtonType.confirm}
    />,
    <IconNavButton
      key={'cancel'}
      onClick={() => dispatch(closeAbonementModal.action())}
      type={ButtonType.cancel}
    />,
  ];

  return (
    <>
    { isAbonementModalOpen && 
      <Modal
        modalMode={modalMode}
        onChangeModalMode={() => dispatch(toggleRuleModalMode.action())}
        comandChilden = {comandChilden}
      >
        <Box
          margin={2}
        >
          <ChipsSelect 
            options={subjectSelectOptions} 
            selected={abonementOfferSubjects} 
            onChange={changeAbonementOfferSubjects}            
          />
          <Select
            marginTop={2}
            marginBottom={2}
            onChange={(id: string) =>  dispatch(changeAbonementType.action(id as AbonementType))}
            options={[
              {id: AbonementType.limited, title:AbonementType.limited },
              {id: AbonementType.unlimited, title: AbonementType.unlimited},
            ]}
            selected={ isUnlimited? AbonementType.unlimited : AbonementType.limited}
          />
          <Box
            marginBottom={2}
          >
            {isUnlimited ?        
              <NumberField
                onChange={(value) => dispatch(changeAbonementLimitTime.action(value))}
                placeHolder={'Limit time'}
                value={limitTime}
                endAdornment='Month'
              />:
              <NumberField
                onChange={(value) => dispatch(changeAbonementLimitLessons.action(value))}
                placeHolder={'Limit lessons'}
                value={limitLessons}
                endAdornment='Lessons'
              />
            }
          </Box>
          <NumberField
            onChange={(value) => dispatch(changeAbonementPrice.action(value))}
            placeHolder={'placeHolder'}
            value={price}
            endAdornment='GEL'
          />
        </Box>
      </Modal>}
    </>
  );
}
