import { Button, Card, TextField, Typography } from '@mui/material';
import React from 'react';
import styled from '@emotion/styled';


export interface ISearchFieldProps extends React.HTMLAttributes<HTMLInputElement> {
  text: string;
  onChangeSearch: (text:string) => void;
  onSearch: () => any;
  placeHolder?: string;
}

const StyledCard = styled(Card)`
  width: 100%;
  height: 40px;
  border-radius: 4px;
  display: flex;
  margin-bottom: 1rem;
`;

const StyledButton = styled(Button)`
  border-radius: 0px 4px 4px 0px;
  text-transform: none;
  padding: 0 18px;
`;

export const SearchField: React.FC<ISearchFieldProps> = ({text, onChangeSearch, onSearch, placeHolder, ...rest}) => {
   return (
    <StyledCard {...rest} elevation={3}>
      <TextField
        variant='standard'
        fullWidth
        type="search"
        placeholder={placeHolder}
        onChange={(e)=>onChangeSearch(e.target.value)}
        value={text}
        inputProps={{style:{padding:'9px'}}}
      />
      <StyledButton 
        onClick={onSearch}
        variant='contained'
      >
        <Typography >
          {'Search'}
        </Typography>
      </StyledButton>
    </StyledCard>
  );
}
