import { Box } from '@mui/material';
import React from 'react';
import { useDispatch } from 'react-redux';
import { lessonAcceptedView, lessonDiclainedView, lessonPendingView } from '../../commands/navigation/navigation';
import { useTeacherSelector } from '../../store';
import { selectLessonView } from '../../selectors/novigation';
import { LessonView } from '../../store/modal';
import styled from '@emotion/styled';
import { ButtonType, IconNavButton } from '../../../common/component/IconNavButton';

export enum TeacherLessonNavigationControlsClasses {
  lessonAcceptedView = 'lessonAcceptedView',
  lessonPendingView = 'lessonPendingView',
  lessonDiclainedView = 'lessonDiclainedView',
};

const NavigationBox = styled(Box)`
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 4rem;
  width: 100%;
  border-bottom: 1px solid grey;
  margin-bottom: 1rem;
  margin-top: 3rem;
`;

export const TeacherLessonNavigation: React.FC = () => {
  const dispatch = useDispatch();
  const selectedLessonView = useTeacherSelector(selectLessonView);
  const acceptedActive = selectedLessonView === LessonView.accepted;
  const pendingActive = selectedLessonView === LessonView.pending;
  const declainedActive = selectedLessonView === LessonView.declained;
  return (
    <NavigationBox>
      <IconNavButton 
        type={ButtonType.accepted} 
        onClick={() => dispatch(lessonAcceptedView.action())} 
        isActive={acceptedActive}
        title='Accepted'
      />
      <IconNavButton 
        type={ButtonType.pending} 
        onClick={() => dispatch(lessonPendingView.action())} 
        isActive={pendingActive}
        title='Pending'
      />
      <IconNavButton 
        type={ButtonType.declained} 
        onClick={() => dispatch(lessonDiclainedView.action())} 
        isActive={declainedActive}
        title='Declained'
      />
    </NavigationBox>
  );
}
