import React from 'react';
import { AppLayoutFull } from '../../../common/component/AppLayout';
import { TeacherScheduleCalendar } from '../TeacherScheduleCalendar';
import { TeacherScheduleHeader } from '../TeacherScheduleHeader';
import { useDispatch } from 'react-redux';
import { navigateSchedule, navigateSubjectList } from '../../commands/navigation/navigation';

export const TeacherSchedule: React.FC = () => {
    const dispatch = useDispatch();
    return (
        <>
            <TeacherScheduleHeader />
            <AppLayoutFull 
                onSwipeRight={() => dispatch(navigateSchedule.action())} 
                onSwipeLeft={() => dispatch(navigateSubjectList.action())}
            >
                <TeacherScheduleCalendar />
            </AppLayoutFull>
        </>
        
    );
}
