import { Alert, Box, Typography } from '@mui/material';
import React from 'react';
import { IRule } from '../../../common/types/domain';
import { getPeriodicityMessage, getTimeRepresentation } from '../../../common/utilities/getWeekDayRepresentation';
import { useTeacherSelector } from '../../store';
import { selectSubjectRules } from '../../selectors/editable';
import { useDispatch } from 'react-redux';
import { startDeletingRule, startEditingRule } from '../../commands/domain/domain';
import { HOC } from '../../../common/hoc/types';
import { Card } from '../../../common/component/Card';
import { IconButtonMenu } from '../../../common/component/IconButtonMenu';

interface ISubjectRuleCardProps extends IRule {
    editCommand: () => void; 
    deleteCommand: () => void;
}

const RuleCard: React.FC<ISubjectRuleCardProps> = ({ timeStart, durationMin, periodicity, editCommand, deleteCommand  }) => {
  const ruleText = getPeriodicityMessage(periodicity);
  
  const timeStartText = getTimeRepresentation(periodicity, timeStart);
  return (
    <Card
      children={[
        <Typography 
          noWrap
          textOverflow='ellipsis'
          overflow='hidden'
          maxWidth='17rem'
          key="ruleText"
        >
          {ruleText}
        </Typography>,
        <IconButtonMenu
          options={[
            {title: 'Edit', command: editCommand},
            {title: 'Delete', command: deleteCommand},
          ]}
          key="menu"
        />,
        <Typography key="duration">{`${durationMin} min`}</Typography>,
        <Typography key="timeStart">{timeStartText}</Typography>
      ]}
      onClick={editCommand}
    />
  );
}

export const SubjectRules: HOC = () => {
  const rules: IRule[] = useTeacherSelector(selectSubjectRules);
  const dispatch = useDispatch();

  return (
    <Box
      flexGrow={1}
      overflow='auto'
    >
     {!!rules.length ? rules.map((rule) => 
       <RuleCard 
         key={rule.id}
         editCommand={() => dispatch(startEditingRule.action(rule.id))} 
         deleteCommand={() => dispatch(startDeletingRule.action(rule.id))} 
         {...rule} 
       />):
       <Alert severity='info'>
         No rules
       </Alert>
     }
    </Box>
  );
}
