import React, { useEffect, useRef } from 'react';
import { RuleModal } from '../RuleModal';
import { SubjectAddRule } from '../SubjectAddRule';
import { SubjectCalendar } from '../SubjectCalendar';
import { SubjectDescription } from '../SubjectDescription';
import { SubjectHeader } from '../SubjectHeader';
import { SubjectRules } from '../SubjectRules';
import { AppLayoutFull } from '../../../common/component/AppLayout';
import { Box } from '@mui/material';
import { useTeacherSelector } from '../../store';
import { selectIsNewRuleModalOpen, selectIsSubjectShowMore, selectRuleModalMode } from '../../selectors/novigation';
import { RulePreviewCalendar } from '../RulePreviewCalendar';
import styled from '@emotion/styled';
import { ModalMode } from '../../../common/component/types';

export const SubjectPage: React.FC = () => {

  const isModalOpen = useTeacherSelector(selectIsNewRuleModalOpen);
  const modalMode = useTeacherSelector(selectRuleModalMode);
  const isOpenShowMore = useTeacherSelector(selectIsSubjectShowMore);

  const isHalfHeight = modalMode === ModalMode.halfHeight;
  const showPreviewCalendar = isModalOpen && isHalfHeight;

  const [descriptionHeight, setContentConterHeight] = React.useState(300);
  const contentConterHeight = `calc(100% - ${descriptionHeight}px)`;

  const descriptionRef = useRef<HTMLDivElement | null>(null);  

  useEffect(() => {
    if (descriptionRef.current) {
      const descriptionHeightAfter = descriptionRef.current.clientHeight + 50;
      if (descriptionHeightAfter !== descriptionHeight) {
        setContentConterHeight(descriptionHeightAfter);
      }
    }
  }, [descriptionHeight]);

  const StyledBox = styled(Box)`
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    overflow: auto;
  `;


  return (
    <>
      <SubjectHeader />
      <AppLayoutFull onSwipeLeft={() => console.log('swipeLeft')} onSwipeRight={() => console.log('swipeRight')}>
        <StyledBox
          height='100%'
        >
          {!isModalOpen && 
            <>
            <Box ref={descriptionRef}>
              <SubjectDescription />
            </Box>
              <StyledBox
                height={contentConterHeight}
              >
                < SubjectCalendar/>        
                {!isOpenShowMore && <SubjectRules/>}
              </StyledBox>
            </>
          }
        </StyledBox>
        <SubjectAddRule />
      </AppLayoutFull>
      {showPreviewCalendar && <RulePreviewCalendar />}
      <RuleModal />
    </>
  );
}
