import React from 'react';
import { useTeacherSelector } from '../../store';
import { useDispatch } from 'react-redux';
import { navigateLesson, onChengeDateBackward, onChengeDateForward, onToday } from '../../commands/external/external';
import { id } from '../../../common/types/domain';
import { selectExternalLessonsData } from '../../selectors/external';
import { selectCurrentDate, selectTeacherFullCalendarView } from '../../selectors/novigation';
import { Calendar } from '../../../common/component/Calendar';

export enum TeacherScheduleCalendarControlsClasses {
    navigateLesson = 'navigateLesson',
};

export const TeacherScheduleCalendar: React.FC = () => {
    const externalLessons = useTeacherSelector(selectExternalLessonsData);
    const teacherCalendarRepresentationType = useTeacherSelector(selectTeacherFullCalendarView);
    const currentDate = useTeacherSelector(selectCurrentDate);
    
    const dispatch = useDispatch();
    const lessonClickHandler = (id: id) => {
        dispatch(navigateLesson.action(id))
    };
    
    return (
        <Calendar 
            lessons={externalLessons} 
            onLessonClick={lessonClickHandler} 
            representationType={teacherCalendarRepresentationType}
            startDate={currentDate}
            onChengeDateBackward={() => dispatch(onChengeDateBackward.action())}
            onChengeDateForward={() => dispatch(onChengeDateForward.action())}
            onToday={() => dispatch(onToday.action())}
        />
    );
}
