import { useDispatch } from 'react-redux';
import { navigateSchedule, navigateSubjectList } from '../../commands/navigation/navigation';
import { selectTeacherView } from '../../selectors/novigation';
import { useTeacherSelector } from '../../store';
import { TeacherView } from '../../store/modal';
import { selectUserProfilePhotoUrl } from '../../../common/selectors/commonSelectors';
import { HOC } from '../../../common/hoc/types';
import { Footer } from '../../../common/component/Footer';
import { ButtonType, IconNavButton } from '../../../common/component/IconNavButton';

export enum TeacherFooterControlsClasses {
    navigateSchedule = 'navigateSchedule',
    navigateSubjectList = 'navigateSubjectList',
};

export const TeacherFooter: HOC = () => {
    const dispatch = useDispatch();
    const photoUrl = useTeacherSelector(selectUserProfilePhotoUrl);

    const selectedTeacherView = useTeacherSelector(selectTeacherView);
    const scheduleActive = selectedTeacherView === TeacherView.schedule;
    const subjectListActive = selectedTeacherView === TeacherView.subjectList;
    return (
        <Footer>
            <IconNavButton
                className={TeacherFooterControlsClasses.navigateSchedule}
                type={ButtonType.schedule}
                onClick={() => dispatch(navigateSchedule.action())}
                isActive={scheduleActive}
            />
            <IconNavButton
                className={TeacherFooterControlsClasses.navigateSubjectList}
                type={ButtonType.avatar}
                onClick={() => dispatch(navigateSubjectList.action())}
                isActive={subjectListActive}
                image={photoUrl}
            />
        </Footer>
    );
}
