import React from 'react';
import { selectLessonView } from '../../selectors/novigation';
import { useTeacherSelector } from '../../store';
import { LessonView } from '../../store/modal';
import { AddStudentModal } from '../AddStudentModal';
import { AddStudentToLesson } from '../AddStudentToLesson';
import { FinalizeLesson } from '../FinalizeLesson';
import { TeacherLessonAcceptedList } from '../TeacherLessonAcceptedList';
import { TeacherLessonDeclainedList } from '../TeacherLessonDeclainedList';
import { TeacherLessonHeader } from '../TeacherLessonHeader';
import { TeacherLessonNavigation } from '../TeacherLessonNavigation';
import { TeacherLessonPendingList } from '../TeacherLessonPendingList';
import { AppLayoutFull } from '../../../common/component/AppLayout';
import { useDispatch } from 'react-redux';
import { lessonAcceptedView, lessonDiclainedView, lessonPendingView } from '../../commands/navigation/navigation';
import { selectLessonHasTeacherLessonNavigation, selectLessonStatus } from '../../selectors/external';
import { LessonStatus } from '../../../common/types/domain';
import { Box } from '@mui/material';
import { HOC } from '../../../common/hoc/types';

export enum LessonPageControlsClasses {

};

export const LessonPage: React.FC = () => {
  const lessonView = useTeacherSelector(selectLessonView);
  const hasTeacherLessonNavigation = useTeacherSelector(selectLessonHasTeacherLessonNavigation);

  const lessonStatus = useTeacherSelector(selectLessonStatus);

  const isWithoutAddParticipant = lessonStatus === LessonStatus.done || lessonStatus === LessonStatus.canceled;

  const dispatch = useDispatch();

  const handleSwipeLeft = () => {
    switch (lessonView) {
      case LessonView.accepted: {
        dispatch(lessonPendingView.action());
        break;
      }
      case LessonView.pending: {
        dispatch(lessonDiclainedView.action());
        break;
      }
      case LessonView.declained: {
        break;
      }
      default: break;
    }
  };

  const handleSwipeRight = () => {
    switch (lessonView) {
      case LessonView.accepted: {
        break;
      }
      case LessonView.pending: {
        dispatch(lessonAcceptedView.action());
        break;
      }
      case LessonView.declained: {
        dispatch(lessonPendingView.action());
        break;
      }
      default: break;
    }
  };


  
    

  return (
    <>
      <TeacherLessonHeader />
      <AppLayoutFull
          onSwipeLeft={handleSwipeLeft}
          onSwipeRight={handleSwipeRight}
      >
          {hasTeacherLessonNavigation && <TeacherLessonNavigation />}
          <Box
            marginTop={hasTeacherLessonNavigation ? 0 : 2}
          >
            <TeacherLessonStudentsList />
          </Box>
      {!isWithoutAddParticipant && <AddStudentToLesson />}
      </AppLayoutFull>
      <AddStudentModal />
      <FinalizeLesson />
    </>
  );
}

const TeacherLessonStudentsList: HOC = () => {
  const teacherList = useTeacherSelector(selectLessonView);
  const lessonStatus = useTeacherSelector(selectLessonStatus);

  const hasTeacherLessonNavigation = useTeacherSelector(selectLessonHasTeacherLessonNavigation);

  const isAccepted = teacherList === LessonView.accepted;
  const isPending = teacherList === LessonView.pending;
  const isDeclained = teacherList === LessonView.declained;

  const isScheduled = lessonStatus === LessonStatus.scheduled;

  return (
    <>
      {hasTeacherLessonNavigation ? 
        <>
          {isAccepted && <TeacherLessonAcceptedList />}
          {isPending && <TeacherLessonPendingList />}
          {isDeclained && <TeacherLessonDeclainedList />}
        </> :
        <>
          {isScheduled ? <TeacherLessonPendingList /> : <TeacherLessonAcceptedList />}
        </>
      }
    </>
  );
}
