import { useTeacherSelector } from '../../store';
import { selectIsLessonAddParticipantModalOpen } from '../../selectors/novigation';
import { closeLessonAddParticipiantModal, toggleRuleModalMode } from '../../commands/navigation/navigation';
import { useDispatch } from 'react-redux';
import { Box, styled } from '@mui/material';
import { UserCard } from '../TeacherLessonDeclainedList';
import { selectExternalUsersData, selectExternalUsersFiltersSearch } from '../../selectors/external';
import { addStudentToLesson, changeAddParticipantSearch, searchParticipantToAdd } from '../../commands/external/external';
import { HOC } from '../../../common/hoc/types';
import { Modal } from '../../../common/component/Modal';
import { IconNavButton, ButtonType } from '../../../common/component/IconNavButton';
import { SearchField } from '../../../common/component/SearchField';
import { ModalMode } from '../../../common/component/types';

const ModalContantContainer = styled(Box)`
  height: 100%;
`;

const showCentreStyles: React.CSSProperties = {
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
};

export const AddStudentModal: HOC = () => {
  const isOpen = useTeacherSelector(selectIsLessonAddParticipantModalOpen);
  const participantsToView = useTeacherSelector(selectExternalUsersData);
  const partisiapntSearch = useTeacherSelector(selectExternalUsersFiltersSearch);

  const hasParticipants = participantsToView.length > 0;

  const dispatch = useDispatch();

  const comandChilden = [
    <IconNavButton 
      onClick={()=>dispatch(closeLessonAddParticipiantModal.action())} 
      type={ButtonType.cancel}
      key='closeLessonAddParticipiantModal'
    />,
  ];

  return (
    <Modal 
      comandChilden={comandChilden} 
      modalMode={isOpen? ModalMode.fillHeight : ModalMode.closed} 
      onChangeModalMode={() => dispatch(toggleRuleModalMode.action())}
     >
      <ModalContantContainer
        sx={!hasParticipants ? showCentreStyles : {}}
      >
        <SearchField 
          text={partisiapntSearch} 
          onChangeSearch={(participantSearch)=>dispatch(changeAddParticipantSearch.action(participantSearch))} 
          onSearch={() => dispatch(searchParticipantToAdd.action())}
        />
        {hasParticipants &&
          <Box>
            {participantsToView.map((participant) => {
              return (
                <UserCard 
                  {...participant}
                  key={participant.id}
                  command={() => dispatch(addStudentToLesson.action(participant.id))} 
                  actionButtomTitle={ 'Send invite to lesson'} 
                />
              );
            })}
          </Box>
        }
      </ModalContantContainer>
    </Modal>
  );
}
