import { useTeacherSelector } from '../../store';
import { selectIsNewRuleModalOpen, selectRuleModalMode } from '../../selectors/novigation';
import { Box } from '@mui/material';
import { PeriodicityType } from '../../../common/types/domain';
import { 
  selectRuleDuration, 
  selectRulePeriodicityRule, 
  selectRulePeriodicityType,
  selectRuleStartTime, 
} from '../../selectors/editable';
import { useDispatch } from 'react-redux';
import { 
  changeRuleDuration, 
  changeRuleMonthDay, 
  changeRulePeriodicityType,
  changeRuleStartTime, 
  toggleRuleWeekDay 
} from '../../commands/editable/editable';
import { confirmSavingRule } from '../../commands/domain/domain';
import { cancelModifyRule, toggleRuleModalMode } from '../../commands/navigation/navigation';
import { HOC } from '../../../common/hoc/types';
import { getDateWithTimeStart } from '../../commands/RulePreviewCalendarFunctions/getDateWithTimeStart';
import { Modal } from '../../../common/component/Modal';
import { IconNavButton, ButtonType } from '../../../common/component/IconNavButton';
import { Select } from '../../../common/component/Select';
import { DatePiker, DataPikerType } from '../../../common/component/DatePiker';
import { NumberField } from '../../../common/component/NumberField';
import { WeekDayPiker } from '../../../common/component/WeekDayPiker';
import { ModalMode } from '../../../common/component/types';

export const RuleModal: HOC = () => {
  const isModalOpen = useTeacherSelector(selectIsNewRuleModalOpen);
  const modalMode = useTeacherSelector(selectRuleModalMode);
  
  const dispatch = useDispatch();
      
  const periodicityRule = useTeacherSelector(selectRulePeriodicityRule);
  const periodicityType = useTeacherSelector(selectRulePeriodicityType);
  const timeStart = useTeacherSelector(selectRuleStartTime);
  const durationMin = useTeacherSelector(selectRuleDuration);
    
  const isSingleEvent = periodicityType === PeriodicityType.manualDate;
  const placeHolder = 'Duration';

  const currentDate = isSingleEvent ? new Date(periodicityRule[0]) : new Date();

  const currentDateWithTimeStart = isSingleEvent ? currentDate : getDateWithTimeStart(currentDate, timeStart);

  const comandChilden = [
    <IconNavButton 
      onClick={()=>{
        dispatch(confirmSavingRule.action())
      }} 
      type={ButtonType.confirm} 
      key='confirmSavingRule'
    />,
    <IconNavButton 
      onClick={()=>dispatch(cancelModifyRule.action())} 
      type={ButtonType.cancel} 
      key='cancelModifyRule'
    />,
  ];

  return (
    <>
      {isModalOpen && 
        <Modal
          comandChilden={comandChilden} 
          modalMode={isModalOpen? modalMode : ModalMode.closed} 
          onChangeModalMode={() => dispatch(toggleRuleModalMode.action())}
          resizable        
        >
          <Box
            margin={2}
          >
            <Select
              marginBottom={2}
              onChange={(periodicityType) => dispatch(changeRulePeriodicityType.action(periodicityType as PeriodicityType))}
              options={[
                {id: PeriodicityType.manualDate, title: PeriodicityType.manualDate },
                {id: PeriodicityType.weekly, title: PeriodicityType.weekly},
              ]}
              selected={isSingleEvent ? PeriodicityType.manualDate : PeriodicityType.weekly}
            />
            {
              <Box>
                {isSingleEvent ?
                  <DatePiker
                    currentDate={currentDateWithTimeStart}
                    type={DataPikerType.DateTime}
                    onChange={
                      (day: Date) => {
                        dispatch(changeRuleMonthDay.action(day.getTime()));
                        dispatch(changeRuleStartTime.action(day));
                      }
                    }
                  /> :
                  <Box>
                    <WeekDayPiker
                      onChange={(day: number) => dispatch(toggleRuleWeekDay.action(day))}
                      checked={periodicityRule}
                    />
                    <DatePiker
                      currentDate={currentDateWithTimeStart}
                      type={DataPikerType.Time}
                      onChange={(timeStart: Date) => dispatch(changeRuleStartTime.action(timeStart))}
                    />
                  </Box>
                }
              </Box>  
            }
            <NumberField
              onChange={(value) => dispatch(changeRuleDuration.action(value))}
              placeHolder={placeHolder}
              value={durationMin}
              endAdornment='min'
            />  
          </Box>         
        </Modal>
      }
    </> 
  );
}
