import { AppLayoutFull } from '../../../common/component/AppLayout';
import { selectSubjectListView } from '../../selectors/novigation';
import { useTeacherSelector } from '../../store';
import { SubjectListView } from '../../store/modal';
import { SubjectListAbonementOfferModal } from '../SubjectListAbonementOfferModal';
import { SubjectListAddAbonementOffer } from '../SubjectListAddAbonementOffer';
import { SubjectListAddSubject } from '../SubjectListAddSubject';
import { SubjectListHeader } from '../SubjectListHeader';
import { SubjectListList } from '../SubjectListList';
import { SubjectListNavigation } from '../SubjectListNavigation';
import { TeacherProfile } from '../TeacherProfile';
import { useDispatch } from 'react-redux';
import { subcectListViewSubjects, subjectListViewAbonements } from '../../commands/navigation/navigation';
import { HOC } from '../../../common/hoc/types';
import { AbonementsList } from '../AbonementsListï»¿';

export const SubjectList: HOC = () => {
  const dispatch = useDispatch();

  return (
    <>
      <SubjectListHeader />
      <AppLayoutFull 
        onSwipeLeft={() => dispatch(subcectListViewSubjects.action())} 
        onSwipeRight={() => dispatch(subjectListViewAbonements.action())}
      >
        <TeacherProfile />
        <SubjectListNavigation />
        <SubjectWindow />
      </AppLayoutFull>
    </>    
  );
}

const SubjectWindow: HOC = () => {
  const subjectView = useTeacherSelector(selectSubjectListView);
  switch (subjectView) {
    case SubjectListView.subjects: {
      return (
        <>
          <SubjectListList />
          <SubjectListAddSubject />
        </>
      )
    }
    case SubjectListView.abonements: {
      return (
        <>
          <AbonementsList />
          <SubjectListAddAbonementOffer />
          <SubjectListAbonementOfferModal />
        </>
      )
    }
    default: return <></>;
  }
}
