import { useDispatch } from 'react-redux';
import { useTeacherSelector } from '../../store';
import { 
  selectIsSubjectNameEditing, 
  selectSubjectName 
} from '../../selectors/editable';
import { Box, Typography } from '@mui/material';
import { confirmSavingSubject } from '../../commands/domain/domain';
import { cancelModifySubject } from '../../commands/navigation/navigation';
import { 
  changeSubjectName, 
  confirmChangeSubjectName, 
  startChangeSubjectName, 
} from '../../commands/editable/editable';
import { HOC } from '../../../common/hoc/types';
import { ClickOutsideHandler } from '../../../common/component/ClickOutsideHandler';
import { Header } from '../../../common/component/Header';
import { TextField, TextFieldType } from '../../../common/component/TextField';
import { IconButton } from '../../../common/component/IconButton';
import { ButtonType } from '../../../common/component/IconNavButton';

export enum SubjectHeaderControlsClasses {
  startChangeTeacherName = 'startChangeTeacherName',
  cancelModifySubject = 'cancelModifySubject',
  confirmSavingSubject = 'confirmSavingSubject',
  confirmChangeSubjectName = 'confirmChangeSubjectName',
};

export const SubjectHeader: HOC = () => {
  const dispatch = useDispatch();
  const subjectName = useTeacherSelector(selectSubjectName);
  const isNameEditing = useTeacherSelector(selectIsSubjectNameEditing);

  return (
    <ClickOutsideHandler onClickOutside={() => {
        isNameEditing && dispatch(confirmChangeSubjectName.action())
      }}
    >
      <Header>
        <IconButton 
          type={ButtonType.cancel} 
          onClick={() => dispatch(cancelModifySubject.action())}
        />
        {isNameEditing ?
            <Box width="100%" overflow="auto" height="100%">
              <TextField
                type={TextFieldType.header} 
                text={subjectName}
                onChange={(text) =>  dispatch(changeSubjectName.action(text))}
              />
            </Box>
           :
          <Typography
            noWrap
            textOverflow='ellipsis'
            overflow='hidden'
            maxWidth='17rem'
            onClick={() => dispatch(startChangeSubjectName.action())}
          >
            {subjectName ? subjectName : 'add title your subject'}
          </Typography>
        }
        <IconButton 
          type={ButtonType.confirm} 
          onClick={() => {
            dispatch(confirmSavingSubject.action())
          }}
        />
      </Header>
    </ClickOutsideHandler>
  );
}
