import { Avatar, Box, Typography } from '@mui/material';
import React from 'react';
import { useDispatch } from 'react-redux';
import { useTeacherSelector } from '../../store';
import { selectDeclainedLessonParticipants, selectLessonParticipantlistHasActionMenu } from '../../selectors/external';
import { IUser } from '../../../common/types/domain';
import { Card } from '../../../common/component/Card';
import { IconButtonMenu } from '../../../common/component/IconButtonMenu';
import { requestStatusChange } from '../../commands/external/external';
import { WarningMasage } from '../../../common/component/WarningMasage';
export interface IUserCardProps extends IUser {
 command: () => void;
 actionButtomTitle: string;
}

export const UserCard: React.FC<IUserCardProps> = ({id, avatar, name, command, actionButtomTitle }) => {
  return (
    <Card
    key={id}
    children={[
      <Box display='flex' alignItems='center'>
          <Avatar sizes='large' src={avatar} />
          <Typography padding='0px 10px' variant='h6'> {name}</Typography>        
      </Box>,
      <>
        {actionButtomTitle  &&  
          <IconButtonMenu 
            options={[
              {
                title: actionButtomTitle, 
                command,
              }]}
          />
        }
      </>
    ]}
    onClick={()=>{}}
  />
  );
}

export const TeacherLessonDeclainedList: React.FC = () => {
  const dispatch = useDispatch();
  const participantsToView = useTeacherSelector(selectDeclainedLessonParticipants);
  const hasActionMenu = useTeacherSelector(selectLessonParticipantlistHasActionMenu);

  return (
    <>
      {!!participantsToView.length?
          <Box
            className='declained list'
          >
            {participantsToView.map((participant) => {
              return (
                <UserCard 
                  {...participant} 
                  command={() => dispatch(requestStatusChange.action(participant.id))} 
                  actionButtomTitle={hasActionMenu ? 'Request status change (Add)' : ''}/>
              );
            })}
          </Box> : <WarningMasage className='declained warning masage' mesage='the Declained list is empty'/> }
    </>
  );
}
