import React from 'react';
import { NotificationButton } from '../NotificationButton';
import { useDispatch } from 'react-redux';
import { setTecherCalendarRepresentationType } from '../../commands/navigation/navigation';
import { TeacherCalendarRepresentationType } from '../../store/modal';
import { useTeacherSelector } from '../../store';
import { selectTeacherCalendarRepresentationType } from '../../selectors/novigation';
import { Header } from '../../../common/component/Header';
import { DropDownMenu, IOption } from '../../../common/component/DropDownMenu';

export enum TeacherScheduleHeaderControlsClasses {

};

export const TeacherScheduleHeader: React.FC = () => {
    const dispatch = useDispatch();
    const scheduleMenuOptions: IOption[] = [
        {
            id: TeacherCalendarRepresentationType.schedule,
            title: 'Schedule',
        },
        {
            id: TeacherCalendarRepresentationType.calendarWeek,
            title: 'Week',
        },
    ];
    const currentOption = useTeacherSelector(selectTeacherCalendarRepresentationType);
    return (
        <Header>
            <DropDownMenu
                options={scheduleMenuOptions}
                title='Abonement'
                currentOption={currentOption}
                onChange={(id) => {
                    const representationType = id as TeacherCalendarRepresentationType.calendarWeek;
                    dispatch(setTecherCalendarRepresentationType.action(representationType));
                }}
            />
            <NotificationButton />
        </Header>
    );
}
