import React from 'react';
import { useDispatch } from 'react-redux';
import { useTeacherSelector } from '../../store';
import { selectLessonParticipantlistHasActionMenu, selectPendingLessonParticipants } from '../../selectors/external';
import { Box } from '@mui/material';
import { UserCard } from '../TeacherLessonDeclainedList';
import { sendAnotherNotification } from '../../commands/external/external';
import { WarningMasage } from '../../../common/component/WarningMasage';


export const TeacherLessonPendingList: React.FC = () => {
  const dispatch = useDispatch();
  const participantsToView = useTeacherSelector(selectPendingLessonParticipants);
  const hasActionMenu = useTeacherSelector(selectLessonParticipantlistHasActionMenu);

  return (
    <>
      {!!participantsToView.length?
        <Box>
          {participantsToView.map((participant) => {
            return (
              <UserCard 
                {...participant} 
                command={() => dispatch(sendAnotherNotification.action(participant.id))} 
                actionButtomTitle={hasActionMenu ? 'Send another notification' : ''}/>
            );
          })}
        </Box> : <WarningMasage mesage='the Pending list is empty'/> }
    </>
  );
}
 
