import React from 'react';
import { useDispatch } from 'react-redux';
import { useTeacherSelector } from '../../store';
import { selectAcceptedLessonParticipants, selectLessonParticipantlistHasActionMenu } from '../../selectors/external';
import { UserCard } from '../TeacherLessonDeclainedList';
import { Box } from '@mui/material';
import { removeStudentFromLesson } from '../../commands/external/external';
import { WarningMasage } from '../../../common/component/WarningMasage';

export enum TeacherLessonAcceptedListControlsClasses {

};

export const TeacherLessonAcceptedList: React.FC = () => {
  const dispatch = useDispatch();
  const participantsToView = useTeacherSelector(selectAcceptedLessonParticipants);
  const hasActionMenu = useTeacherSelector(selectLessonParticipantlistHasActionMenu);
  return (
    <>
      {!!participantsToView.length?
        <Box>
          {participantsToView.map((participant) => {
            return (
              <UserCard 
                {...participant} 
                command={() => dispatch(removeStudentFromLesson.action(participant.id))} 
                actionButtomTitle={hasActionMenu ? 'Student Absant (remove)' : ''}/>
            );
          })}
        </Box> : <WarningMasage mesage='the Accepted list is empty'/> }
    </>
  );
}
      