import { Avatar, Box, Button, Typography } from '@mui/material';
import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { alreadyAuthenticatedUser, authenticateFacebook, authenticateWithFirebase } from '../../commands/authentication';
import { selectUserProfile, selectUserProfileAvatar } from '../../../common/selectors/commonSelectors';
import { useTeacherSelector } from '../../store';
import { signOutCommand } from '../../../common/commands';
import { onAuthStateChanged } from 'firebase/auth';
import { auth } from '../../../common/api/firebaseConnection';
import { HOC } from '../../../common/hoc/types';

export enum AuthenticationControlsClasses {

};

export const Authentication: HOC = () => {
  const dispatch = useDispatch();
  const user = useTeacherSelector(selectUserProfile);
  const avatar = useTeacherSelector(selectUserProfileAvatar);
  const isUserSigned = !!user.id;

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (user) => {
      if (user) {
        dispatch(alreadyAuthenticatedUser.action(user));
      }

      return () => unsubscribe();
    });
  }, [dispatch])
  
    return (
      <Box>
        { isUserSigned && (
          <Box display="flex" margin="30px 0">
            <Box width="4rem" height="4rem">
              <Avatar src={avatar} />
            </Box>

            <Box>
                <Typography variant='h4'>{user.displayName}</Typography>
                <Typography>{user.email}</Typography>
                <Typography>{user.providerId}</Typography>
                <Typography>{user.id}</Typography>
            </Box>
          </Box>
        ) }

        {!isUserSigned ? (
          <>
            <Button onClick={() => dispatch(authenticateWithFirebase.action())}>Sign in with Google</Button>
            <Button onClick={() => dispatch(authenticateFacebook.action())}>Sign in with Facebook</Button>
          </>
        ) : (
          <>
            <Button onClick={() => dispatch(signOutCommand.action())}>Sign out</Button>
          </>
        )}
      </Box>
      
    );
}
