import { useDispatch } from 'react-redux';
import { cancelLesson } from '../../commands/external/external';
import { useTeacherSelector } from '../../store';
import { selectLesson } from '../../selectors/external';
import { LessonStatus } from '../../../common/types/domain';
import { HOC } from '../../../common/hoc/types';
import { LessonHeader } from '../../../common/component/LessonHeader';

export enum TeacherLessonHeaderControlsClasses {
  cancelLesson = 'cancelLesson'
};

export const TeacherLessonHeader: HOC = () => {
  const dispatch = useDispatch();
  const lesson = useTeacherSelector(selectLesson);
  return (
    <LessonHeader 
      type={lesson.status as LessonStatus}
      scheduled={lesson.date}
      title={lesson.subject.name}
      onCancel={() => dispatch(cancelLesson.action())}
    />
  );
}
